



















































import { Component, Prop, Vue } from "vue-property-decorator";
import Axios from "axios";

@Component
export default class ComponentName extends Vue {
  loading: boolean = true;
  disabled: boolean = false;
  tableData: Array<object> = [];

  pickerOptions: any = {
    disabledDate(time: any) {
      return time.getTime() >= Date.now();
    },
    shortcuts: [
      {
        text: "上一日",
        onClick(picker: any) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24);
          picker.$emit("pick", [start, start]);
        },
      },
      {
        text: "今日",
        onClick(picker: any) {
          const end = new Date();
          const start = new Date();
          //start.setTime(start.getTime() - 3600 * 1000 * 24);
          picker.$emit("pick", [start, start]);
        },
      },
      {
        text: "最近一周",
        onClick(picker: any) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
          picker.$emit("pick", [start, end]);
        },
      },
    ],
  };

  public value2: Array<string> = [
    this.formatDate(
      new Date(new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 1))
    ),
    this.formatDate(
      new Date(new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 1))
    ),
  ];

  public thousandSeparator(num: number) {
    return (
      num &&
      (num.toString().indexOf(".") != -1
        ? num.toString().replace(/(\d)(?=(\d{3})+\.)/g, function($1, $2) {
            return $2 + ",";
          })
        : num.toString().replace(/(\d)(?=(\d{3})+\b)/g, function($1, $2) {
            return $2 + ",";
          }))
    );
  }

  btn() {
    //console.log(this.startTime.setTime(this.star));
    // console.log(this.value2);
    // console.log(this.thousandSeparator(4656262.262))

    // this.tableData = [];
    this.getData(this.value2[0].toString(), this.value2[1].toString());
  }
  private mounted() {
    console.log(this.value2);
    this.getData(this.value2[0].toString(), this.value2[1].toString());
  }
  public getData(s: string, d: string) {
    this.disabled = true;
    this.loading = true;
    Axios.get("http://58.42.231.98:5060/api/ptraffic/time", {
      params: {
        start: s,
        end: d,
      },
    })
      .then((res) => {
        if (res.status == 200) {
          //{"tbheader":"psquare","tbdata":2000},{"tbheader":"phm","tbdata":3000}
          this.tableData = [];
          let resData = res.data[0];
          let tbheader;
          for (let j in resData) {
            switch (j) {
              case "psquare":
                tbheader = "沿湖广场";
                break;
              case "phm":
                tbheader = "H&M";
                break;
              case "pgateway":
                tbheader = "人行天桥";
                break;
              case "pparking":
                tbheader = "停车场";
                break;
              default:
                continue;
            }

            this.tableData.push({
              tbheader: tbheader,
              tbdata: this.thousandSeparator(resData[j]),
            });
            //console.log(j+":"+resData[j]);
          }
          this.loading = false;
          this.disabled = false;
          console.log(this.tableData);
        }
      })
      .catch((err) => {
        console.log(err);
        this.disabled = false;
      });
  }

  public formatDate(date: Date) {
    var d = date,
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [year, month, day].join("-");
  }
  public getSummaries(param:any) {
        const { columns, data } = param;
        const sums:any = [];
        columns.forEach((column:any, index:any) => {
          if (index === 0) {
            sums[index] = '总计';
            return;
          }
          const values:any = data.map((item: any) => Number(item[column.property].replace(',','')));
          if (!values.every((value: any) => isNaN(value))) {
            sums[index] = values.reduce((prev:any, curr:any) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] = this.thousandSeparator(sums[index]) + ' (人)';
          } else {
            sums[index] = '';
          }
        });

        console.log(sums)

        return sums;
      }
}
